<template>
  <div class="story" v-hammer:press="() => stop = true">
    <header class="story__header">
      <Progress :count="storyItems.length" :time="time" :activeNumber="activeStoryItemIndex"/>
      <div class="story__info">
        <CircleImg :src="storyData.avatar" :title="storyData.title"/>
        <span class="story__title">{{ storyData.title }}</span>
      </div>
    </header>
    <Emoji title="Hızlı Cevap" @selectEmoji="sendEmoji"/>
    <Media
      v-if="activeItem && story.id === storyData.id"
      :type="activeItem.type"
      :src="activeItem.mediaUrl"
      @load="loadMedia"
    />
    <footer class="story__footer">
      <Button type="circle" size="md" class="-m-right-10">
        <File accept="image/*" @change="replyPhoto">
          <Icon name="Camera"/>
        </File>
      </Button>
      <InputText
        placeholder="Mesaj Gönder"
        v-model="message"
        @keyup.enter="messageHandler"
        @focus="focusHandler"
      >
        <Button>
          <Icon name="More"/>
        </Button>
      </InputText>
    </footer>
  </div>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";

export default {
  name: "Story",
  props: {
    storyData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      time: 0,
      message: "",
      focusMessage: false,
      stop: false,
      interval: null
    };
  },
  computed: {
    ...mapGetters({
      story: "activeStory",
      activeStoryItemIndex: "activeStoryItemIndex",
      activeUser: "user"
    }),
    storyItems() {
      return this.storyData.stories;
    },
    emojiVisible() {
      return !this.message;
    },
    activeItem() {
      return this.storyItems[this.activeStoryItemIndex];
    }
  },
  watch: {
    emojiVisible(status) {
      this.toggleEmoji();
    },
    stop(status) {
      if (!status) {
        this.timer();
      }
    }
  },
  methods: {
    ...mapMutations([
      "changeModal",
      "setActiveStoryItemIndex",
      "toggleEmoji",
      "sendMessage"
    ]),
    loadMedia() {
      this.timer();
    },
    focusHandler() {
      this.toggleEmoji();
    },
    timer() {
      if (this.interval) return;
      let currentTime = 0;
      const activeIndex = this.activeStoryItemIndex;
      const time = this.activeItem.duration || 5;
      const progress = () => {
        if (currentTime >= time || activeIndex !== this.activeStoryItemIndex) {
          this.time = 0;
          clearInterval(this.interval);
          this.interval = null;
          this.setActiveStoryItemIndex(this.activeStoryItemIndex + 1);
        } else {
          currentTime += 1;
          this.time = (currentTime / time) * 100;
        }
      };
      this.interval = setInterval(progress, 1000);
    },
    sendEmoji(emoji) {
      this.sendMessage({
        message: emoji,
        story: this.storyData,
        storyMedia: this.storyItems[this.activeStoryItemIndex],
        sender: this.activeUser
      });
      this.toggleEmoji();
    },
    messageHandler() {
      this.sendMessage({
        message: this.message,
        story: this.storyData,
        storyMedia: this.storyItems[this.activeStoryItemIndex],
        sender: this.activeUser
      });
    },
    replyPhoto(files) {
      const file = files[0];
      this.changeModal({
        name: "Reply",
        props: {
          media: file,
          story: this.storyData
        }
      });
    }
  }
};
</script>


