<template>
  <div class="story-modal" v-hammer:swipe.all="onSwipe">
    <Close class="story-modal__close"/>
    <Cube3d :rotateY="rotateY">
      <template slot="back">
        <Story v-if="nextStory" :storyData="nextStory"/>
        <Story v-else-if="story" :storyData="story"/>
        <Story v-else-if="prevStory" :storyData="story"/>
      </template>
      <template slot="left">
        <Story v-if="prevStory" :storyData="prevStory"/>
      </template>
      <template slot="front">
        <Story v-if="story" :storyData="story"/>
      </template>
      <template slot="right">
        <Story v-if="nextStory" :storyData="nextStory"/>
      </template>
    </Cube3d>
  </div>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
export default {
  name: "StoryModal",
  data() {
    return {
      time: 0,
      rotateY: 0
    };
  },
  computed: {
    ...mapGetters({
      story: "activeStory",
      prevStory: "prevStory",
      nextStory: "nextStory",
      activeStoryItemIndex: "activeStoryItemIndex"
    })
  },
  methods: {
    ...mapMutations([
      "toggleModal",
      "goNextStory",
      "goPrevStory",
      "setActiveStoryItemIndex"
    ]),
    onSwipe(e) {
      switch (e.type) {
        case "swipedown":
          this.swipeDown();
          break;
        case "swipeleft":
          this.swipeLeft();
          break;
        case "swiperight":
          this.swipeRight();
          break;
      }
    },
    swipeDown() {
      this.toggleModal();
    },
    swipeRight() {
      this.setActiveStoryItemIndex(0);
      this.rotateY += 90;
      setTimeout(() => {
        this.goPrevStory();
      }, 500);
      // if (this.activeStoryItemIndex > 0) {
      //   this.setActiveStoryItemIndex(this.activeStoryItemIndex - 1);
      // }
    },
    swipeLeft() {
      this.setActiveStoryItemIndex(0);
      this.rotateY -= 90;
      setTimeout(() => {
        this.goNextStory();
      }, 500);
      // if (this.activeStoryItemIndex < this.story.stories.length - 1) {
      //   this.setActiveStoryItemIndex(this.activeStoryItemIndex + 1);
      // }
    }
  }
};
</script>

<style lang="scss">
.story-modal {
  height: 100%;

  &__close {
    position: absolute;
    top: 30px;
    right: 10px;
    z-index: 99;
  }
}
</style>
