<template>
  <textarea
    v-model="val"
    :style="style"
    class="canvas-text"
    name="textarea"
    v-bind="$attrs"
    v-on="$listeners"
    v-hammer:pan.all="onPan"
  ></textarea>
</template>

<script>
export default {
  name: "CanvasText",
  props: {
    value: {},
    font: {
      type: String
    },
    color: {
      type: String
    }
  },
  data() {
    return {
      val: this.value,
      lastX: 0,
      lastY: 0,
      isDragging: false,
      coordinat: {
        x: 0,
        y: 0
      }
    };
  },
  watch: {
    val(val) {
      this.$emit("input", val);
    }
  },
  computed: {
    style() {
      return {
        font: this.font,
        color: this.color
      };
    }
  },
  methods: {
    onPan(ev) {
      const elem = ev.target;

      if (!this.isDragging) {
        this.isDragging = true;
        this.lastX = elem.offsetLeft;
        this.lastY = elem.offsetTop;
        elem.style.transform = "none";
      }

      const posX = ev.deltaX + this.lastX;
      const posY = ev.deltaY + this.lastY;
      elem.style.left = `${posX}px`;
      elem.style.top = `${posY}px`;

      this.coordinat = {
        x: posX,
        y: posY
      };
    }
  }
};
</script>

<style lang="scss">
.canvas-text {
  border: none;
  background: transparent;
  color: #fff;
  font-size: 30px;
  resize: none;
  min-height: 50px;
  max-height: 100%;
  max-width: 100%;
  overflow: hidden;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;

  &:focus,
  &:active {
    outline: none;
    box-shadow: none;
    -webkit-box-shadow: none;
    box-shadow: none;
  }

  &::-moz-focus-inner {
    border: 0;
  }

  &::placeholder {
    color: currentColor;
    opacity: 1;
  }

  &:-ms-input-placeholder {
    color: currentColor;
  }

  &::-ms-input-placeholder {
    color: currentColor;
  }
}
</style>

